@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-top: v.$separacion;
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;

        label {
            flex: 0 0 10rem;
        }
        input {
            flex: 1;
            padding: 1rem;
            border: none;
            border-radius: 6px;
            background-color: lighten(v.$gris, 35%);
        }
    }
    
}