@use 'base/variables' as v;
@use 'base/mixins' as m;

.login {
    margin-top: v.$separacion;
    .uptask{
        @include m.textoDegradado(v.$indigo);
    }

    input[type="submit"] {
        @include m.boton (v.$indigo, v.$blanco);
    }

    .acciones {
        a {
            color: v.$indigo;
        }
    }
}